export default {
  stringify(layerData){
    /** ANCHOR: Функция зависит от структуры типа: Phaser.Tilemaps.DynamicTilemapLayer*/
    return layerData.layer.data
      .map(r=>r.map(c=>c.index))
      .map(r=>r.join(' ')).join('|');
  },
  parse(layerData){
    return layerData
      .split('|')
      .map(r => r.split(' ').map(i => +i))
  },
}