import LayerFormat from './layerFormatter'

const emptyFiller = -1;

function createEmptyMap(width, height){
  return new Array(height).fill([]).map(()=>new Array(width).fill(emptyFiller));
}
function resizeLayer(layer, width, height){
  let res = layer.map(r=> r.length >= width ? r.splice(0,width) : r.concat(new Array(width-r.length).fill(emptyFiller)));
  if(res.length >= height){
    res.length = height;
  }else{
    res = res.concat(createEmptyMap(width, height-layer.length))
  }
  return res;
}

export default function layerProcess(layerData_str, width, height){
  let layer = layerData_str ? LayerFormat.parse(layerData_str) : createEmptyMap(width, height);
  return resizeLayer(layer, width, height);
}