import Convert from './convert'

// import LayerFormat from './project_editor/layerFormatter'
import LayerProcess from './project_editor/layerProcess'

const key_searchName = 'n';

function tryJSONParse(data){
  return typeof data === 'string' ? Convert.from.json(data) : data;
}

const browserLang = navigator.language || navigator.userLanguage; 
const intlDate = new Intl.DateTimeFormat(browserLang, {
  weekday: 'long',
  year: 'numeric',
  month: 'short',
  day: '2-digit',
  hour: 'numeric',
  minute: 'numeric',
});

export default class ProjectDataParser {

    constructor(projectData = {}) {
      this.project = projectData;

      this.maps = this.project.m || [];
      this.images = this.project.i || {};
      this.anims = this.project.n || [];
      this.audio = this.project.a || {};
      this.datas = this.project.d || {};
      this.rules = this.project.r || [];
      this.manifest = this.project.mn || {};
      this.cellSize = this.manifest.cs || 16;
    }


    
    // NOTE: MANIFEST
    v(){return this.manifest.v_s}
    projName(deflt='', proj = this.project){return tryJSONParse(proj).mn.n || deflt}
    projDescr(deflt='', proj = this.project){return tryJSONParse(proj).mn.d || deflt}
    projPreview(proj = this.project){return tryJSONParse(proj).mn.pv || tryJSONParse(proj).mn.pv_a || 'img/preview.png'}
    isHasAutoPreview(proj = this.project){return tryJSONParse(proj).mn.pv_a != null}
    projTimeUpd(proj = this.project){
      const date = Convert.from.time.minutes(proj.mn.t_u)
      return intlDate.format(date).replace(/^./, l=>l.toUpperCase());
    }


    // NOTE: ANIMATIONS
    animData(id){
      const anim = this.anims[id];
      const sprite = this.imgByCAI(anim._c, anim.i);
      return {
        fWidth: anim.w,
        fHeight: anim.h,
        sprite,
        fStart: anim.fs,
        fEnd: anim.fe,
        fRate: anim.s,
        yoyo: anim.yy,
      }
    }
    animCfg(scene, categ, id, texName){
      const anim = this.anims.filter(a=>a._c===categ)[id];
      // console.log(`animCfg(${[scene, categ, id, texName].join(', ')}):`);
      // console.table({filtered: this.anims.filter(a=>a._c===categ), anim});
      // texName;
      return {
        // _fh: anim.h,
        // _fw: anim.w,
        key: ''+id,
        frames:
          anim.fs !== anim.fe
          ? scene.anims.generateFrameNumbers(texName, { start: anim.fs, end: anim.fe })
          : [ { key: texName, frame: anim.fs } ],
        frameRate: anim.s,
        yoyo: anim.yy,
        repeat: anim.r || -1,
      }
    }

    // NOTE: AUDIOS
    audAll(){
      return {
        e: this.audio.e.map((a, id)=>({ id, name: a.n, info: a.i, data: a.d })),
        m: this.audio.m.map((a, id)=>({ id, name: a.n, info: a.i, data: a.d })),
      }
    }

    // NOTE: IMAGES
    imgByCAI(category, ingId){
      try {return this.images[category][ingId].d;
      } catch (e) {return ''}
    }
    imgNameByCAI(category, ingId){
      try {return this.images[category][ingId][key_searchName];
      } catch (e) {return ''}
    }


    // NOTE: MAPS
    mapSize(mapIx_OR_map){
      const manifest = typeof mapIx_OR_map === 'number' ? this.maps[mapIx_OR_map].m : mapIx_OR_map.m;
      return {
        width: manifest.w,
        height: manifest.h,
      }
    }
    mapColor(mapIx_OR_map){
      const manifest = (typeof mapIx_OR_map === 'number' ? this.maps[mapIx_OR_map].m : mapIx_OR_map.m) || '000000';
      return +('0x' + manifest.c);
    }
    mapData(mapIx){
      const map = this.maps[mapIx];
      const size = this.mapSize(map);
      const layerList = map.l;
      return {
        // b: LayerProcess(layerList.b, width, height),
        data: {
          blocksProps: map.d.p,
        },
        layers: {
          s: Convert.from.json(layerList.s || {}),
          m: LayerProcess(layerList.m, size.width, size.height),
        },
      }
    }
    // mapLayers(mapIx){
    //   const map = this.maps[mapIx];
    //   const size = this.mapSize(map);
    //   const layerList = map.l;
    //   return {
    //     // b: LayerProcess(layerList.b, width, height),
    //     s: Convert.from.json(layerList.s || {}),
    //     m: LayerProcess(layerList.m, size.width, size.height),
    //   }
    // }
    mapGravity(mapIx){
      const map = this.maps[mapIx];
      return map.m.p_g;
    }
      


    // NOTE: DATA
    d_blocks(){return this.datas.b || []}
      sumBlockDataList(){
        const blocks = Convert.copy(this.d_blocks());
        blocks.forEach(b => {
          b.i = this.imgByCAI('b', b.i);
        });
        return blocks;
      }
      collisblBlocksIds(){
        const ids = [];
        this.d_blocks().forEach((b, ix) => {
          if(b.is_p) ids.push(ix)
        });
        return ids;
      }
      blocksTextures(){
        return this.sumBlockDataList().map(b=>b.i||b.c);
      }
    d_structs(){return this.datas.s || []}
      sumStructDataList(){
        const structs = Convert.copy(this.d_structs());
        return structs.map(s => ({
          name        : s.n,
          texture     : this.imgByCAI('s', s.i),
          width       : s.w * this.cellSize,
          height      : s.h * this.cellSize,
          color       : s.c,
          isPhysical  : s.is_p,
        }));
      }
      structsTextures(){
        return this.sumStructDataList().map(b=>b.texture||b.color);
      }
      structsSizeList(){
        return this.d_structs().map(s=>[
          s.w * this.cellSize, 
          s.h * this.cellSize,
        ]);
      }
    d_entities(){return this.datas.e || []}
      getEntityData(ix){
        const ent = this.d_entities()[ix];
        
        let anims = (ent.al||'').split(' ').filter(v=>v.length).map(v=>+v);
        if(anims[0]===''){ anims = null }

        return {
          tid     : ix,
          name    : ent.n,
          rules   : ent.rls,
          categ   : 'e',
          descr   : ent.d,
          width   : ent.w * this.cellSize,
          height  : ent.h * this.cellSize,
          anims,
          sprite  : typeof ent.i === 'number' ? this.imgByCAI('e', ent.i) : '',
          color   : +('0x'+ent.c) || 0x005500,
          velX    : ent.vx,
          velY    : ent.vy,
          maxVelX : ent.mx,
          maxVelY : ent.my,
          acelX   : ent.ax,
          acelY   : ent.ay,
          dragX   : ent.dx,
          dragY   : ent.dy,
        }
      }


    // NOTE: RULES
    // r_rules(){return this.datas.b}


}