import LS from "./localStorage";
import Convert from "./convert";

Object.assign(window, {
  $shared: {},    // Передача данных между контекстами (нп, Vuex - Game Engine)
  $libs: {},      // Переменные загруженных библиотек для движка
  $comps: {},     // Переменные загруженных программных компонентов движка
  $info: {},      // Информация о состояниях и пр
  $data: {        // Данные, ресурсы и пр проекта
    aud: {},
    blocks: [],
    isDebug: false,
    editObjectType: 0,
    sBlockId: 0,
    sBlockIdAlt: -1,
    sStructId: 0,
    sStructIdAlt: -1,
    // maps: [],
    // sdMapId: 0,
    sLayerKey: 'm',
  }, 
  $triggers: {},
  $events: {},
})

function clearTriggers() {
  Object.assign(window.$triggers, {
    start: ()=>0,
    setPause: ()=>0,
    destroy: ()=>0,
    getMapData: ()=>0,
    setAudioSettings: ()=>0,
    setLayer: ()=>0,
    setBlockPropsEdit: ()=>0,
  });
}
function clearEvents(){
  Object.assign(window.$events, {
    onEngineLoad: ()=>0,
  });
}
clearTriggers();
clearEvents();
window.$triggers.clear = clearTriggers;



const LSK_audiostgs = 'SB_as';
let counter = 0;
function setAudStgs(audioSettings){
  let audStgs = audioSettings || LS.load(LSK_audiostgs, {}, Convert.from.json);
  if(audStgs==null){
    audStgs = {vol: 0.1}
  }
  window.$data.aud = audStgs;
  window.$triggers.setAudioSettings(audStgs);
  LS.save(LSK_audiostgs, audStgs, Convert.to.json);

  console.log('Вызов №'+(counter++)+'\nЗначение: ')
  console.dir(audStgs)
}
setAudStgs();

export default {
  setAudStgs,
  clearTriggersAndEvents(){
    clearTriggers();
    clearEvents();
  }, 
  setData(obj){
    Object.assign(window.$data, obj)
  },
  // setMaps(mapDataList){
  //   window.$data.maps = mapDataList;
  // },
  // addMap(mapData){
  //   window.$data.maps.push(mapData);
  // },
}